.ContactForm {
    margin: 20px auto;
    width: 80%;
    text-align: center;
    box-sizing: border-box;
    box-shadow: 0 2px 3px #ccc;
    padding: 10px;
    border: 1px solid #eee;
}

.ContactForm input {
    display: block;
}

@media(min-width:600px) {
    .ContactForm {
        width: 500px;
    }
}